<script>
import { TimelineLite, Sine } from 'gsap'


export default {
    name: "QuoteHomePage",
    mounted (){
      const { quote, quoteTwo, speaker } = this.$refs
      const timeline = new TimelineLite()

      timeline.to(quote, .25, {
           opacity: 1,
            y: -5,
           ease: Sine.easeIn,
        }, .25) 

        timeline.to(quoteTwo, .25, {
           opacity: 1,
           y: -5,
           ease: Sine.easeIn,
        }, .5) 

        timeline.to(speaker, .25, {
           opacity: 1,
           y: -5,
           ease: Sine.easeIn,
        }, .75) 

    }
}

</script>


